import Overture from './views/Overture';
import About from './views/About';
import Project from './views/Project';

export default function routeConfig(router) {
	router.map({
		'/': {
			name: 'Overture',
			component: Overture
		},
		'/about': {
			name: 'About',
			component: About
		},
		'/project/:codeName': {
			name: 'Project',
			component: Project
		}
	});

	router.redirect({
		'*': '/'
	});
}


