<template>
	<div class="background">
		<img :src="backgroundImg" class="background-img"/>
	</div>
	<div class="stage" :class="{'lights-on': lights, 'lights-off': !lights}">
		<router-view></router-view>
	</div>
</template>

<script>
	import Gallery from './Gallery'

	export default {
		components: {},
		ready() {},
		data() {
			return {
				Gallery: Gallery,
				backgroundImg: 'https://unsplash.it/800/600/?random',
				lights: true
			}
		},
		methods: {},
		events: {
			'nowPlaying': function (src) {
				this.backgroundImg = src;
			},
			'lightsOn': function () {
				this.lights = true;
			},
			'lightsOff': function () {
				this.lights = false;
			}
		}
	}
</script>


