import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App'

Vue.use(VueRouter)

import './style.less'

import routerInit from './routers'
const router = new VueRouter()
routerInit(router)

router.start(Vue.extend(App), 'app')
window.router = router


